* {
  padding: 0;
  margin: 0;
}

.book-skin {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  min-height: 300px;
}
.book-skin a {
  text-decoration: none;
  color: #333;
}
.book-skin .book-title {
  line-height: 100px;
  text-align: center;
}
.book-skin .book-title .book-nav > li {
  list-style: none;
  display: none;
}
.book-skin .book-title .book-nav > li.active {
  display: inline-block;
  font-size: 30px;
}
.book-skin .book-title .book-nav > li.book-back {
  position: absolute;
  left: 0;
}
.book-skin .book-title .book-nav > li.book-search {
  position: absolute;
  right: 0;
}
.book-skin .book-title .book-nav > li.book-search .fa {
  position: absolute;
  top: 40px;
  left: 5px;
  font-size: 20px;
  color: #f9cfa6;
}
.book-skin .book-title .book-nav > li.book-search input {
  font-size: 20px;
  padding-left: 25px;
  background: transparent;
  border: 1px solid #f9cfa6;
  display: none;
}
.book-skin .book-title .book-nav > li.book-search .search-tip {
  position: absolute;
  top: 63px;
  z-index: 99;
  line-height: 25px;
  background: #f9cfa6;
  width: 100%;
  display: none;
}
.book-skin .book-title .book-nav > li.book-search .search-tip li {
  list-style: none;
}
.book-skin .book-title .book-nav > li.book-search .search-tip li:hover {
  background: #af5d0d;
  color: #fff;
}
.book-skin .book-title .book-nav > li.book-search input:focus + .search-tip, .book-skin .book-title .book-nav > li.book-search.focus .search-tip {
  display: block;
}
.book-skin .book-title .book-nav > li.book-search.focus input {
  display: inline-block;
}
.book-skin .book-title .book-nav:hover > li, .book-skin .book-title .book-nav.hover > li {
  display: inline-block;
}
.book-skin .book-body {
  margin-right: 200px;
  background: #f9cfa6;
  padding: 30px 15px;
  min-height: 800px;
  position: relative;
}
.book-skin .book-body .book-sort {
  position: absolute;
  right: 25px;
  top: -30px;
}
.book-skin .book-body .book-sort a {
  display: inline-block;
  width: 50px;
  line-height: 30px;
  text-align: center;
  position: relative;
  background: #f9cfa6;
}
.book-skin .book-body .book-sort a:after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  left: 0;
  bottom: -50px;
  border: transparent 25px solid;
  border-top-color: rgba(0, 0, 0, 0.6);
}
.book-skin .book-body .book-sort a.active {
  background: #af5d0d;
  color: #fff;
}
.book-skin .book-body .book-sort a.active:after {
  border-top-color: #af5d0d;
}
.book-skin .book-body .book-item {
  border-bottom: 1px solid #af5d0d;
  padding: 5px;
}
.book-skin .book-body .book-item dt {
  font-size: 20px;
  font-weight: 600;
}
.book-skin .book-body .book-item dt .book-time {
  float: right;
}
.book-skin .book-body .book-item dd {
  display: none;
}
.book-skin .book-body .book-item dd p {
  margin: 5px 0;
}
.book-skin .book-body .book-item dd .author,
.book-skin .book-body .book-item dd .comment,
.book-skin .book-body .book-item dd .category {
  margin-right: 20px;
}
.book-skin .book-body .book-item dd .agree,
.book-skin .book-body .book-item dd .disagree {
  float: right;
  margin-left: 10px;
}
.book-skin .book-body .book-item:hover dd {
  display: block;
}
.book-skin .book-body .info {
  border-bottom: 1px dotted #af5d0d;
  line-height: 40px;
}
.book-skin .book-body .info span {
  margin-right: 20px;
}
.book-skin .book-body .content {
  margin-bottom: 40px;
}
.book-skin .book-body .tools {
  border-top: 1px dotted #af5d0d;
  line-height: 40px;
  position: absolute;
  bottom: 0;
  right: 15px;
  left: 15px;
  font-size: 20px;
}
.book-skin .book-body .tools .agree,
.book-skin .book-body .tools .disagree {
  float: right;
  margin-left: 20px;
}
.book-skin .book-footer .book-pager {
  float: left;
}
.book-skin .book-footer .book-pager li {
  list-style: none;
  background: #f9cfa6;
  width: 40px;
  line-height: 40px;
  text-align: center;
  float: left;
  border-top: 1px dotted #af5d0d;
}
.book-skin .book-footer .book-pager li.active {
  background: #af5d0d;
  color: #fff;
}
.book-skin .book-footer .book-pager li.active:hover {
  height: 40px;
}
.book-skin .book-footer .book-pager li:hover {
  height: 50px;
}
.book-skin .book-footer.comment {
  margin-top: 50px;
  margin-right: 200px;
  background: #f9cfa6;
  padding: 15px;
  position: relative;
}
.book-skin .book-footer .book-comments .title {
  font-size: 20px;
  border-left: 5px solid #af5d0d;
  padding-left: 10px;
  margin-top: 15px;
}
.book-skin .book-footer .book-comments .title .order {
  float: right;
  font-size: 12px;
}
.book-skin .book-footer .book-comments .title .order .active {
  color: #f00;
}
.book-skin .book-footer .book-comments .comment-item {
  padding: 8px 0;
  border-bottom: 1px solid #af5d0d;
  position: relative;
}
.book-skin .book-footer .book-comments .comment-item .info {
  line-height: 30px;
}
.book-skin .book-footer .book-comments .comment-item .info .user {
  font-weight: 600;
}
.book-skin .book-footer .book-comments .comment-item .info .time {
  margin-left: 20px;
  color: #767676;
}
.book-skin .book-footer .book-comments .comment-item .info .floor {
  float: right;
}
.book-skin .book-footer .book-comments .comment-item .content p {
  margin: 5px 0;
}
.book-skin .book-footer .book-comments .comment-item .content .expand {
  font-size: 12px;
}
.book-skin .book-footer .book-comments .comment-item .content .comment,
.book-skin .book-footer .book-comments .comment-item .content .report {
  float: right;
  margin-left: 20px;
}
.book-skin .book-footer .book-comments .comment-item .actions {
  position: absolute;
  right: -215px;
  width: 200px;
  text-align: center;
  top: 30px;
  background: #f9cfa6;
  font-size: 20px;
  line-height: 40px;
}
.book-skin .book-footer .book-comments .comment-item .actions span {
  padding: 0 3px;
}
.book-skin .book-footer .book-comments .comment-item .comments {
  padding-left: 100px;
  margin-top: 8px;
  border-top: 1px solid #af5d0d;
}
.book-skin .book-footer .book-comments .comment-item .comments .comment-item:last-child {
  border: none;
  padding-bottom: 0;
}
.book-skin .book-footer .book-comments.hot-comments .title {
  margin-top: 0;
}
.book-skin .book-footer .book-comments.hot-comments .comment-item .comments {
  display: none;
}
.book-skin .book-footer .book-comments.hot-comments .comment-item.active .comments {
  display: block;
}
.book-skin .book-footer .book-comments .book-comment-form .btn-cancel {
  display: inline-block;
}
.book-skin .book-chapter {
  position: absolute;
  width: 200px;
  right: 0;
  top: 130px;
}
.book-skin .book-chapter li {
  background: #f9cfa6;
  list-style: none;
  width: 150px;
  line-height: 30px;
  border-bottom: 1px solid #af5d0d;
  padding-left: 20px;
  box-sizing: border-box;
}
.book-skin .book-chapter li .fa {
  margin-right: 5px;
}
.book-skin .book-chapter li.active {
  background: #af5d0d;
  color: #fff;
  width: 200px;
}
.book-skin .book-chapter li.active:hover {
  width: 200px;
}
.book-skin .book-chapter li.active a {
  color: #fff;
}
.book-skin .book-chapter li:hover {
  width: 175px;
}
.book-skin .book-dynamic {
  position: absolute;
  width: 200px;
  right: 0;
  top: 400px;
}
.book-skin .book-dynamic dl {
  background: #f9cfa6;
  border-bottom: 1px solid #af5d0d;
}
.book-skin .book-dynamic dl dt {
  line-height: 30px;
  font-size: 12px;
}
.book-skin .book-dynamic dl dt a {
  font-size: 16px;
  font-weight: 600;
}
.book-skin .book-dynamic dl dd {
  display: none;
}
.book-skin .book-dynamic dl dd .book-time {
  text-align: right;
  display: block;
}
.book-skin .book-dynamic dl dd p {
  margin: 5px 0;
}
.book-skin .book-dynamic dl:hover dt {
  background: #af5d0d;
  color: #fff;
}
.book-skin .book-dynamic dl:hover dd {
  display: block;
}

.book-comment-form .title {
  font-size: 20px;
  border-left: 5px solid #af5d0d;
  padding-left: 10px;
  margin-top: 15px;
}
.book-comment-form .form-table {
  display: table;
  width: 100%;
}
.book-comment-form .form-table .form-group {
  display: table-row;
}
.book-comment-form .form-table .form-group label {
  line-height: 30px;
  width: 200px;
  display: table-cell;
  text-align: center;
}
.book-comment-form .form-table .form-group input,
.book-comment-form .form-table .form-group select {
  display: table-cell;
  background: transparent;
  border: none;
  border-bottom: 1px solid #af5d0d;
  font-size: 20px;
  width: 100%;
}
.book-comment-form textarea {
  width: 100%;
  height: 100px;
  resize: none;
  background: transparent;
  border: 1px solid #af5d0d;
  font-size: 20px;
}
.book-comment-form button {
  line-height: 30px;
  background: #af5d0d;
  padding: 0 20px;
  border: none;
}
.book-comment-form .btn-cancel {
  display: none;
}

.book-clear {
  clear: both;
}

.footer {
  margin-top: 50px;
  padding: 20px;
  text-align: center;
}